.gallery {
    background: rgba($blue, 0.07);
    padding-top: 50px;
    padding-bottom: 80px;
    .slick-track{
        display: flex;
        gap: 32px;

    }
    &__inner {
        overflow: hidden;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 50px;
    }
    &__link {
        color: $blue-text;
        transition: color 0.1s linear;
        text-decoration: underline;
        margin-right: 55px;

        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        &:hover {
            color: $active-link;
        }
    }

    &__arrow {
        cursor: pointer;
        padding: 16px 20px 16px 18px;
        border-radius: 50%;
        border: 1px solid $blue-elem ;
    }

    &__arrow--left {
        margin-right: 20px;
        padding: 16px 20px 16px 18px;
    }

    &__arrow--right {
        transform: rotate(-180deg);
    }

    &__items {
        display: flex;
        flex-direction: column;
    }

    &__img {
        &+&{
            margin-top: 32px;
        }
    }
}
