.intro {
    $intro: &;
    width: 100vw;
    height: 100vh;
    background: url(../img/intro.png);
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: cover;
    .container {
        height: 100%;
        position: relative;
    }
    &__inner {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
    }

    &__content {
        color: white;
    }

    &__title {
        font-family: PT Serif;
        font-size: calc(40px + 50 * (100vw / 1135));
        // 40 calc(40px + 50 * (100vw / 1135))
        font-weight: 700;   
        line-height: calc(52px + 65 * (100vw / 1135)) ;
        // 52 calc(52px + 65 * (100vw / 1135))
    }

    &__subtitle {
        font-family: Roboto;
        font-size: calc(14px + 6 * (100vw / 1135));
        //14 calc(14px + 6 * (100vw / 1135))
        font-weight: 400;
        line-height: calc(20px + 8 * (100vw / 1135));
        // 20 calc(20px + 8 * (100vw / 1135))
    }

    &__btn {
        margin-top: 70px;
        width: 250px;
        height: 60px;
        display: flex;
        justify-content: center;
        align-items: center;

        font-family: Roboto;
        font-size: 18px;
        font-weight: 500;
        line-height: 21px;
        text-transform: uppercase;
    }

    &__arrow {
        position: absolute;
        left: 50%;
        transform: translate(-50%);
        bottom: 30px;
        border: none;
        background: transparent;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        border: 1px solid #fff;
        transition: background 0.1s linear;
        &:hover{
            background: #fff;
            #{$intro}__arrow-img {
                path{
                    stroke: black;
                }
            }
        }
    }

    &__arrow-img {
        margin-top: 2px;
        path{
            transition: stroke 0.1s linear;
        }
    }
}
