.about {
    margin-top: 76px;
    margin-bottom: 80px;
    &__inner {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__left {
        max-width: 408px;
        position: relative;
    }

    &__title {
        margin-bottom: 20px;
    }
    &__text {
        font-family: Roboto;
        font-size: 18px;
        font-weight: 400;
        line-height: 25px;
        color: $main;
    }

    &__link {
        font-family: Roboto;
        font-size: 16px;
        font-weight: 400;
        line-height: 19px;
        color: $text-help;
        text-decoration: underline;

        position: absolute;
        bottom: 0;
        left: 0;

        transition: color 0.1s linear;
        &:hover{
            color: $active-link;
        }
    }

    &__video-block {
        position: relative;
        height: 100%;
    }
    &__video {
        height: 100%;
        
    }

    &__darkning {
        z-index: 3;
        position: absolute;
        top: 0;
        left: 0;
        height: calc(100% - 2px);
        width: 100%;
        background: rgba(#000, 0.3);
        display: block;
        &--active {
            opacity: 0;
        }
    }
    &__start {
        cursor: pointer;
        position: absolute;
        z-index: 6;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        &--active {
            opacity: 0;
        }
    }
}
